tem name=\"1\">\r\n          <Icon type=\"ios-paper\" />文档\r\n        </MenuItem>\r\n        <MenuItem name=\"2\">\r\n          <Icon type=\"ios-people\" />DEMO\r\n        </MenuItem>\r\n        <Submenu name=\"3\">\r\n          <template slot=\"title\">\r\n            <Icon type=\"ios-stats\" />统计分析\r\n          </template>\r\n          <MenuGroup title=\"使用\">\r\n            <MenuItem name=\"3-1\">新增和启动</MenuItem>\r\n            <MenuItem name=\"3-2\">活跃分析</MenuItem>\r\n            <MenuItem name=\"3-3\">时段分析</MenuItem>\r\n          </MenuGroup>\r\n          <MenuGroup title=\"留存\">\r\n            <MenuItem name=\"3-4\">用户留存</MenuItem>\r\n            <MenuItem name=\"3-5\">流失用户</MenuItem>\r\n          </MenuGroup>\r\n        </Submenu>\r\n        <MenuItem name=\"4\">\r\n          <Icon type=\"ios-construct\" />历史改进\r\n        </MenuItem>\r\n      </Menu>\r\n    </div>\r\n    <div id=\"content\">\r\n      <div>\r\n        <div class=\"logo\">\r\n          <img src=\"https://i2.tiimg.com/700479/5ae4ce384fd00748.png\" />\r\n          <h1>krabby patty</h1>\r\n          <h3>蟹黄堡web框架原型机</h3>\r\n        </div>\r\n      </div>\r\n      <Divider />\r\n\r\n      <Row>\r\n        <Col span=\"18\">\r\n          <div style=\"border-right:1px solid #e8eaec\">\r\n            <h1 id=\"preface\">写在前面</h1>\r\n            <p>- 这是我自己开发的框架.在此之前我一直使用的是koa/egg框架进行开发,但是过程中发现有很多不尽我意的地方,为了能够更顺手的开发web程序,我决定自己开发</p>\r\n            <p>\r\n              - 名字的来源是两个朋友的名字联合,因此我称它为<b>蟹黄堡</b>\r\n            </p>\r\n            <p>\r\n              - 开发的中心思想是完全符合<b>我</b>的开发习惯,以及尽可能的<b>模块化</b>和附带<b>智能提示</b>\r\n            </p>\r\n            <h1 id=\"advantage\">优势</h1>\r\n            <p># 组件\\请求\\工具Load在上下文中</p>\r\n            <p># 可以自定义loader及typings</p>\r\n            <p># 非常夸张的深层嵌套洋葱圈模型</p>\r\n            <p># 企图可拆分微服务模块</p>\r\n            <p># 自动重启以及更多的开放性api</p>\r\n            <p># 以约束定义规则</p>\r\n            <h1 id=\"init\">安装及快速开始</h1>\r\n            <div class=\"code\">\r\n              <pre>{{code.init}}</pre>\r\n            </div>\r\n            <h1 id=\"tree\">目录树</h1>\r\n            <h2 id=\"common_tree\">标准目录</h2>\r\n            <p>常用的目录结构</p>\r\n            <div class=\"common-tree\">\r\n              <Tree :data=\"fileTree_1\"></Tree>\r\n            </div>\r\n            <h2 id=\"module_tree\">子模块目录</h2>\r\n            <p>业务需要有嵌套模块的目录结构,此时中间件也是嵌套的</p>\r\n            <div>\r\n              <Tree :data=\"fileTree_2\"></Tree>\r\n            </div>\r\n            <h1 id=\"enter\">入口文件</h1>\r\n            <p>项目从这里开始,并启动一个蟹黄堡实例</p>\r\n            <div class=\"code\">\r\n              <pre>{{code.app}}</pre>\r\n            </div>\r\n            <h1 id=\"module\">Module</h1>\r\n            <p>这里是面包片,功能都在这里组合</p>\r\n            <div class=\"code\">\r\n              <pre>{{code.module}}</pre>\r\n            </div>\r\n            <h1 id=\"controller\">Controller</h1>\r\n            <p>这里是路由和功能组合文件</p>\r\n            <p>\r\n              我是用\r\n              <b>[new Symbol()]</b>作为\r\n              <b>Function</b>名称.因为我认为这里的索引是路由的地址,方法不应该被其他形式的调用\r\n            </p>\r\n            <div class=\"code\">\r\n              <pre>{{code.controller}}</pre>\r\n            </div>\r\n\r\n            <h1 id=\"service\">Service</h1>\r\n            <p>这里是业务功能模块</p>\r\n            <p>\r\n              这里可以在上下文拿到请求的\r\n              <b>body</b>,\r\n              <b>file</b>,\r\n              <b>query</b>,\r\n              <b>header</b>\r\n            </p>\r\n            <div class=\"code\">\r\n              <pre>{{code.service}}</pre>\r\n            </div>\r\n\r\n            <h1 id=\"middleware\">Middleware</h1>\r\n            <p>这里是请求中间件</p>\r\n            <p>中间件的加载顺序为底层base.module -> controller -> router</p>\r\n            <p>多模块的中间件的加载顺序base.module -> 子模块base.module,往下与基本顺序对齐</p>\r\n            <h2 id=\"module_middleware\">module级别中间件</h2>\r\n            <div class=\"code\">\r\n              <pre>{{code.middleware.module}}</pre>\r\n            </div>\r\n            <h2 id=\"controller_middleware\">controller及router级别中间件</h2>\r\n            <div class=\"code\">\r\n              <pre>{{code.middleware.controller}}</pre>\r\n            </div>\r\n          </div>\r\n        </Col>\r\n        <Col span=\"6\">\r\n          <div class=\"catalog\">\r\n            <Anchor show-ink offset-top=\"60\">\r\n              <AnchorLink href=\"#preface\" title=\"写在前面\" />\r\n              <AnchorLink href=\"#advantage\" title=\"优势\" />\r\n              <AnchorLink href=\"#init\" title=\"安装及快速开始\" />\r\n              <AnchorLink href=\"#tree\" title=\"目录树\">\r\n                <AnchorLink href=\"#common_tree\" title=\"标准目录\" />\r\n                <AnchorLink href=\"#module_tree\" title=\"子模块目录\" />\r\n              </AnchorLink>\r\n              <AnchorLink href=\"#enter\" title=\"入口文件\" />\r\n              <AnchorLink href=\"#module\" title=\"Module\" />\r\n              <AnchorLink href=\"#controller\" title=\"Controller\" />\r\n              <AnchorLink href=\"#service\" title=\"Service\" />\r\n              <AnchorLink href=\"#middleware\" title=\"Middleware\">\r\n                <AnchorLink href=\"#module_middleware\" title=\"module级别中间件\" />\r\n                <AnchorLink href=\"#controller_middleware\" title=\"controller及router级别中间件\" />\r\n              </AnchorLink>\r\n            </Anchor>\r\n          </div>\r\n        </Col>\r\n      </Row>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { IVue, IComponent, IWatch } from './interface/vue'\r\nimport code from './code'\r\n\r\n@IComponent({ components: {} })\r\nexport default class App extends IVue {\r\n\tcode = code\r\n\tfileTree_1 = [\r\n\t\t{\r\n\t\t\ttitle: '├module_1',\r\n\t\t\texpand: true,\r\n\t\t\tchildren: [{ title: '├controller' }, { title: '├service' }, { title: '├module_1.module.ts' }]\r\n\t\t},\r\n\t\t{\r\n\t\t\ttitle: '├module_2',\r\n\t\t\texpand: true,\r\n\t\t\tchildren: [{ title: '├controller' }, { title: '├service' }, { title: '├module_2.module.ts' }]\r\n\t\t},\r\n\t\t{ title: '├base.module.ts' },\r\n\t\t{ title: '├package.json' }\r\n\t]\r\n\tfileTree_2 = [{ title: '├controller' }, { title: '├service' }, { title: '├base.module.ts' }, { title: '├package.json' }]\r\n\tmounted() {\r\n\t\tconsole.log(1)\r\n\t}\r\n}\r\n</script>\r\n<style scoped>\r\n#app {\r\n\tbackground: #4aa598;\r\n}\r\n#app .top-nav {\r\n\tposition: fixed;\r\n\ttop: 0;\r\n\twidth: 100%;\r\n\tbackground: #fff;\r\n\tz-index: 2;\r\n}\r\n#content {\r\n\tbackground: #fff;\r\n\twidth: 86%;\r\n\tmin-height: calc(100vh - 60px);\r\n\tmargin: 0 auto;\r\n\tpadding: 32px;\r\n\tfont-size: 14px;\r\n}\r\n#content h1,\r\nh2,\r\nh3 {\r\n\tmargin: 16px 0;\r\n}\r\n#content p {\r\n\tmargin-left: 8px;\r\n}\r\n#app .logo {\r\n\ttext-align: center;\r\n\tmargin-top: 60px;\r\n}\r\n#app .logo img {\r\n\theight: 260px;\r\n\twidth: auto;\r\n}\r\n#app .logo h1 {\r\n\tfont-size: 60px;\r\n}\r\n.layout {\r\n\tborder: 1px solid #d7dde4;\r\n\tbackground: #f5f7f9;\r\n\tposition: relative;\r\n\tborder-radius: 4px;\r\n\toverflow: hidden;\r\n}\r\n.layout-logo {\r\n\twidth: 100px;\r\n\theight: 30px;\r\n\tbackground: #f5f7f9;\r\n\tborder-radius: 3px;\r\n\tfloat: left;\r\n\tposition: relative;\r\n\ttop: 15px;\r\n\tleft: 20px;\r\n}\r\n.common-tree {\r\n\tmin-height: 252px;\r\n}\r\n.code pre {\r\n\tbackground: url(./img/code-block-bg.png) #f5f6f7;\r\n\twidth: 775px;\r\n\tborder-left: 3px solid black;\r\n\tline-height: 24px;\r\n\tfont-size: 16px;\r\n}\r\n.catalog {\r\n\tpadding: 16px;\r\n}\r\n</style>"],"sourceRoot":""}]);

